.welcome {
    text-align: center;
}

.welcome__title {
    font-size: 30px;
    font-weight: 500;
    margin: 8px 0 20px;
}

.welcome__info {
    font-size: 14px;
    margin: 14px;

}

.welcome__info a {
    color: #1890ff;
    text-decoration: none;
}




.search {
    width: 100%;
    height: 32px;
    line-height: 32px;
    padding: 0 11px;
    font-size: 14px;
    color: rgba(0,0,0,.65);
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    margin-top: 15px;
}

.search:hover {
    border-color: #40a9ff;
}

.search:focus {
    border-color: #40a9ff;
    outline: 0;
    box-shadow: 0 0 0 2px rgba(24,144,255,.2);
}


.info {
    color: #314659;
    font-size: 14px;
    margin: 0;
}

.info__done {
    color: #1890ff;
    padding: 5px;
}

.info__left {
    color: #FF4444;
    padding: 5px;
}
.filter {
    border-radius: 3px;
}

.filter__btn {
    background: #fff;
    height: 32px;
    font-size: 14px;
    outline: none;
    line-height: 30px;
    padding: 0 15px;
    color: rgba(0,0,0,.65);
    border: 1px solid #d9d9d9;
    cursor: pointer;
}

.filter__btn:hover {
    color: #40a9ff;
}
.filter__btn:active {
    color: #40a9ff;
}

.filter__btn:first-child {
    border-radius: 4px 0 0 4px;
}
.filter__btn:last-child {
    border-radius: 0 4px 4px 0;
}

.filter__btn.filter__btn--active {
    border-color: #1890ff;
}
.listItem {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 12px 24px;
    border-bottom: 1px solid #e8e8e8;
    position: relative;
}

.listItem.listItem--checked {
    background: rgb(190, 200, 200);
    border-color: rgb(190, 200, 200);
}
.listItem.disabled {
    pointer-events: none;
    border-color: #1890ff;
}
.listItem.disabled:after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #1890ff; 
}
.listItem.listItem--important {
    background: #e6f7ff;
}

.listItem:last-child {
    border-bottom: none;
}

.listItem__wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.listItem__field {
    border: none;
    background: transparent;
    width: 100%;
    margin-right: 20px;
    color: rgba(0,0,0,.65);
    padding: 0 11px;
    font-size: 14px;
    height: 32px;
    line-height: 32px;
    border-radius: 4px;
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.listItem.listItem--checked .listItem__field {
    text-decoration: line-through;
}

.listItem__field.active {
    border: 1px solid #d9d9d9;
    background: #fff;
    cursor: text;
    -webkit-user-select: auto;
       -moz-user-select: auto;
        -ms-user-select: auto;
            user-select: auto;
}
.listItem__field.active:hover {
    border-color: #40a9ff;
}

.listItem__field.active:focus {
    border-color: #40a9ff;
    outline: 0;
    box-shadow: 0 0 0 2px rgba(24,144,255,.2);
}


.listItem__change {
    border: none;
    border-radius: 4px;
    background-color: #1890ff;
    color: #fff;
    font-size: 14px;
    height: 32px;
    line-height: 1.499;
    padding: 0 15px;
    outline: none;
    cursor: pointer;
}

.listItem__delete {
    border: none;
    border-radius: 4px;
    background: red;
    color: #fff;
    font-size: 14px;
    height: 32px;
    margin-left: 8px;
    line-height: 1.499;
    padding: 0 15px;
    cursor: pointer;
    outline: none;
}

.listItem__save {
    border: none;
    border-radius: 4px;
    background: rgb(82, 196, 26);
    color: #fff;
    font-size: 14px;
    height: 32px;
    line-height: 1.499;
    outline: none;
    margin-right: 8px;
    padding: 0 15px;
    cursor: pointer;
}

.listItem__cancel {
    border: 1px solid #d9d9d9;
    box-shadow: 0 2px 0 rgba(0,0,0,.015);
    border-radius: 4px;
    background: #fff;
    color: rgba(0,0,0,.65);
    font-size: 14px;
    height: 32px;
    line-height: 1.499;
    padding: 0 15px;
    outline: none;
    cursor: pointer;
}

.hidden {
    visibility: hidden;
    display: none;
}

.listItem__important {
    border: none;
    border-radius: 4px;
    background: rgb(82, 196, 26);
    color: #fff;
    font-size: 14px;
    height: 32px;
    width: 32px;
    line-height: 1.499;
    text-align: center;
    margin-right: 8px;
    outline: none;
    cursor: pointer;
}

.listItem__important.listItem__important--active {
    background: rgb(57, 125, 23);
}

.listItem__check {
    position: relative;
    height: 16px;
    width: 16px;
    padding-left: 14px;
    cursor: pointer;
    background-color: #fff;
    border: 1px solid #d9d9d9;
    border-radius: 2px;
}

.listItem__check:hover {
    border-color: #1890ff;
}

.listItem__check span {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 8px;
    height: 8px;
    border-radius: 2px;
    background-color: transparent;
}

.listItem.listItem--checked .listItem__check span {
    background-color: #1890ff;
}





.list {
    margin: 0;
    padding-left: 0;
    list-style: none;
    border-radius: 4px;
    overflow: hidden;
    border: 1px solid #d9d9d9;
}
.createItem {
    margin-top: 15px;
    display: -webkit-flex;
    display: flex;
}

.createItem__field {
    height: 32px;
    line-height: 32px;
    width: 100%;
    margin-right: 8px;
    font-size: 14px;
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    padding: 0 11px;
}

.createItem__field:hover {
    border-color: #40a9ff;
}

.createItem__field:focus {
    border-color: #40a9ff;
    outline: 0;
    box-shadow: 0 0 0 2px rgba(24,144,255,.2);
}

.createItem__btn {
    height: 32px;
    line-height: 1.499;
    font-size: 14px;
    background-color: #1890ff;
    color: #fff;
    padding: 0 15px;
    outline: none;
    cursor: pointer;
    border: 1px solid #1890ff;
    border-radius: 4px;
    text-shadow: 0 -1px 0 rgba(0,0,0,.12);
    box-shadow: 0 2px 0 rgba(0,0,0,.045);
}
.createItem__btn:hover {
    background-color: #40a9ff;
    border-color: #40a9ff;
}
.createItem__btn:active {
    background-color: #096dd9;
    border-color: #096dd9;
}


body {
    background: #fff;
    padding: 0;
    margin: 0;
    color: #314659;
    font-family: Arial, sasn-serif;
}

.app {
    background: #fff;
    max-width: 700px;
    width: 100%;
    margin: 40px auto;
    padding: 42px 24px 50px;
    border: 1px solid #ebedf0;
    border-radius: 2px;
}

.app__justify {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin: 15px 0 30px;
}

* {
    box-sizing: border-box;
    -webkit-transition: all .3s;
    transition: all .3s;
}
